        top: 50%;
        transform: translateY(-50%);
    }

    .mcr-box p{
        margin: 20px 0;
        line-height: 160%;
        color: white;
    }

    .mcr-title{
        font-size: 25px;
    }

    .mcr-addr, .mcr-phone, .mcr-email{
