}

@media screen and (max-width:480px){
	.nav-ul{
        width: 90%;
        display: flex;
        justify-content: flex-end;
    }

    .nav-ul-big{
        width: 90%;
        display: flex;
        justify-content: flex-end;
    }

	.side-name{
        display: none;
    }

    /* footer */
    .footer{
        background-color: #fafdf2;
    }

    .footer-div{
        padding: 20px 20px;
        position: relative;
    }

    .footer-box{
        margin: 0 auto;
        max-width: 1080px;
        display: grid;
        grid-template-columns: 0 9fr 0;
        align-items: center;
        width: 100%;
    }

    .footer-content{
        align-items: center;
        font-size: 8px;
        width: 100%;
    }

    .footer-content div{
        display: block;
        justify-content: center;
        margin: 10px ;
        color: #96c93d;
    }

    .footer-content div p{
        margin: 10px;
    }

    .footer-content div p::after{
        content: '';
    }

    /* main-carousel */
    .mCarousel{
        z-index: 1;
    }

    .mCarousel-box{
        z-index: 2;
        width: 100%;
        position: relative;
        overflow: hidden;
    }

    .mCarousel-button{
        position: absolute;
        color: white;
        opacity: 0.6;
        font-size: 25px;
        font-weight: 700;
        top: 50%;
        transform: translateY(-50%);
        cursor: pointer;
        z-index: 3;
    }

    .mCarousel-button:hover{
        opacity: 0.8;
    }

    .mCarousel-button:active{
        opacity: 1;
    }

    .mCarousel-list{
