        height: 35vh;
        display: flex;
        justify-content: flex-start;
        transition: 0.5s all;
    }
    .mCarousel-img img{
        z-index: 0;
        object-fit: cover;
        filter: brightness(78%);
        width: 100%;
        position: absolute;
        transform: translateY(0%);
    }

    .mCarousel-img0 .mCarousel-text{
