        border-right: 1px solid #eee;
    }

    .introduce-contents{
        max-width: 1080px;
        margin: 0 auto;
        display: flex;
        flex-direction: column-reverse;

    }

    .introduce-img{
        height: 500px;
        overflow-y: hidden;
        margin-bottom: 20px;
    }

    .introduce-text{
        position: relative;
    }

    .introduce-text p{
        word-break: keep-all;
        line-height: 160%;
        font-size: 15px;
        color: rgb(75, 75, 75);
    }
    .introduce-name{
        position: absolute;
        right: 0;
        text-align: end;
        bottom: 555px;
        z-index: 13;
