        transform: translateY(-50%);
    }

    .mcr-box p{
        margin: 15px 0;
        line-height: 160%;
        color: white;
    }

    .mcr-title{
        font-size: 20px;
    }

    .mcr-addr, .mcr-phone, .mcr-email{
        font-size: 12px;
    }
