.wrap{
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
}


.mark{
    position: relative;
}


/* header */

.header{
    background-color: transparent;
    top: 0;
    position: fixed;
    z-index: 15;
}

.nav{
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav-img{
    width: 6.5%;
    transition: 0.4s all;
}

.nav-big{
    padding: 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: white;
    border-bottom: 1px solid #eee;
    transition: 0.4s all;
}

.nav-img-big{
    width: 8%;
}

.nav-ul{
    width: 40%;
    display: flex;
    justify-content: flex-end;
}

.nav-ul-big{
    width: 40%;
    display: flex;
    justify-content: flex-end;
}

.nav-ul-big .nav-ul-li a{
    color: black;
}

.nav-ul-li{
    margin-left:25px;
    font-size: 13px;
    font-weight: 700;
    letter-spacing: 1px;
}

.nav-ul-li a{
    color: #d3d3d3;
}

.nav-ul-li a:hover{
    color: rgb(78, 78, 78);
}

.nav-ul-li-big{
    font-size: 16px;
}

/* footer */

.footer{
}

.footer-div{
    padding: 50px;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
    position: relative;
}

.footer-box{
    margin: 0 auto;
    max-width: 1080px;
    display: grid;
    padding: 0 20px;
    grid-template-columns: 1fr 8fr 1fr;
    grid-column-gap: 20px;
    align-items: center;
}


.footer-logo2 img{
    position: relative;
    top: -10px;
}

.footer-content{
    align-items: center;
}

.footer-content div{
    display: flex;
    justify-content: center;
    margin: 20px;
    color: darkgray;
}

.footer-content div p::after{
    content: '|';
    font-size: 11px;
    margin: 0 10px;
    font-weight: 600;
    position: relative;
    top:-2px;
}

/* main-carousel */
.mCarousel{
    z-index: 1;
}

.mCarousel-box{
    z-index: 2;
    width: 100%;
    position: relative;
    overflow: hidden;
}

.mCarousel-button{
    position: absolute;
    color: white;
    opacity: 0.6;
    font-size: 44px;
    font-weight: 700;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    z-index: 3;
}

.mCarousel-button:hover{
    opacity: 0.8;
}

.mCarousel-button:active{
    opacity: 1;
}

.mCarousel-list{
    width: 400%;
    height: 73vh;
    display: flex;
    justify-content: flex-start;
    transition: 0.5s all;
}

.mCarousel-content{
    width: 25%;
}

.mCarousel-img{
    width: 100%;
    position: relative;
    height: 100%;
}

.mCarousel-img img{
    z-index: 0;
    object-fit: cover;
    -webkit-filter: brightness(78%);
            filter: brightness(78%);
    width: 100%;
    position: absolute;
    transform: translateY(-23%);
}

.mc-0{
    transform: translateX(0%);
}

.mc-1{
    transform: translateX(-25%);
}

.mc-2{
    transform: translateX(-50%);
}

.mc-3{
    transform: translateX(-75%);
}

.mCarousel-text{
    position: absolute;
    text-shadow: 1px 1px 2px rgb(122, 122, 122);
}


.mCarousel-img0 .mCarousel-text{
	top:25%;
    left: 10%;
}

.mCarousel-img1 .mCarousel-text{
	top:30%;
    left: 10%;
}

.mCarousel-img2 .mCarousel-text{
	top:35%;
    left: 57%;
    text-align: right;
    color: rgb(231, 231, 231);
    text-shadow: 2px 2px 2px rgb(0, 0, 0);
}

.mCarousel-img3 .mCarousel-text{
	top:28%;
    left: 47%;
}

/* intro */

.intro{
    text-align: center;
    
}

.intro-box{
    padding: 45px;
}

.intro-box p{
    margin: 20px 0;
}

.intro-text1{
    font-size: 25px;
    font-weight: 600;
}

.intro-text2{
    font-size: 19px;
}

/* introduce */
.introduce-box{
    padding: 50px;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
}

.introduce-contents{
    max-width: 1080px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 6fr 4fr;
    grid-column-gap: 20px;
}
.introduce-text{
    position: relative;
}

.introduce-text p{
    word-break: keep-all;
    line-height: 170%;
    font-size: 17px;
    color: rgb(75, 75, 75);
}

.introduce-text p strong{
    font-weight: 600;
    color: black;
}

.introduce-name{
    position: absolute;
    right: 0;
    text-align: end;
    bottom: -3px;
}

.introduce-name p{
    color: black;
    font-weight: 600;
    font-size: 20px;
}

/* business */
.business-box{
    padding: 50px;
    border-left: 1px solid #eee;
    border-right: 1px solid #eee;
    position: relative;
}

.business-box-div{
    max-width: 1080px;
    margin: 0 auto;
}

.business-contents{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 15px;
    grid-row-gap: 10px;
}

.business-img-box{
    width: 100%;
    overflow: hidden;
    text-align: center;
    position: relative;
}

.business-img-box img{
    width: 100%;
    height: 200px;
    object-fit: cover;
    transition: 0.8s all;
}


.business7{
    grid-column-start: span 2;
    grid-column-end: span 3;
}

.business-content{
    position: relative;
    overflow: hidden;
    cursor: pointer;
}

.business-img-border{
    position: absolute;
    width: 100%;
    height: 100%;
    transition: 0.6s all;
    
}
.business-img-box:hover .business-img-border{
    border: 20px solid white;
    
}

.business-content p{
    width: 100%;
    padding: 10px 0;
    transition: 0.6s all;
    font-weight: 500;
}

.business-content:hover .business-content-name{
    /* transform: translate(5.5%, -50%); */
    transform: translateX(5.5%);
    font-weight: 600;
    /* color: #9bdda7; */
}

.business-title{
    padding-bottom: 10px;
}

.main-titles p{
    font-size: 25px;
    margin-bottom: 10px;
}

.eng-title{
    font-weight: 600;
}

/* licenses */
.licenses-contents{
    padding: 20px 0;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}
.licenses-content{
    padding-right: 3px;
    overflow: hidden;
    position: relative;
}

.licenses-content img{
    border: 1px solid #eee;
    box-shadow: 1px 1px #eee;
    cursor: pointer;
    z-index: 0;
}

.licenses-content-text{
    position: absolute;
    bottom: 0;
    z-index: 1;
    padding: 20px;
    text-align: center;
    width: 100%;
    background: #00b09b;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(-125deg, #96c93d, #00b09b); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    opacity: 0;
    transition: 0.7s all;
    color: white;
}

.licenses-content:hover .licenses-content-text{
    opacity: 1;
}

/* map */
.map-contents{
    display:grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 15px;
    height: 400px;
}

.map-contents-right{
    position: relative;
    padding: 0 20px;
}

.mcr-box{
    margin: 0 auto;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.mcr-box p{
    margin: 20px 0;
    line-height: 160%;
    color: white;
}

.mcr-title{
    font-size: 30px;
}

.mcr-addr, .mcr-phone, .mcr-email{
    font-size: 13px;
}

