@import url('https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&family=Noto+Sans+KR&family=Noto+Serif+KR&display=swap');


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	-webkit-touch-callout: none; 
	user-select: none; 
	-moz-user-select: none; 
	-ms-user-select: none; 
	-webkit-user-select: none;

}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
 	padding: 0;
  	margin: 0;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

a{
  text-decoration: none;
  color: black;
  cursor: pointer;
}

div, button, span, input, textarea, a, nav, article, section, form, table, tr, td, th{
  box-sizing: border-box;
}

h1,h2,h3,h4,h5,p {
  rotate: (0.03deg);
}

button{
	cursor: pointer;
	background-color: #eee;
	border: none;
}

div{
	padding:0;
	margin:0;
}

.font-sans{
	font-family: 'Noto Sans KR', sans-serif;
}

.font-serif{
	font-family: 'Noto Serif KR', serif;
}

.font-my{
	font-family: 'Nanum Myeongjo', serif;
}

.side-name{
	position: absolute;
	transform: rotate(90deg);
	font-size: 12px;
	top: 160px;
	left: -140px;
	color: #9bdda7;
	font-weight: 600;
	align-items: center;
}

.test{
	border: 1px solid red;
}

.w15{
	width: 100%;
	max-width: 1150px;
	margin: 0 auto;
}

.w19{
	position: relative;
	width: 100%;
	max-width: 1920px;
	margin: 0 auto;
	border-bottom: 1px solid #eee;
}

.w19-h{
	position: relative;
	width: 100%;
	max-width: 1920px;
	margin: 0 auto;
}
.mark1{
	position: relative;
}
.mark2{
	position: relative;
}
.mark2::before{
	position: absolute;
	content: "+";
	font-weight: 400;
	top: -10px;
	left: -6px;
	z-index: 2;
}

.mark2::after{
	position: absolute;
	content: "+";
	z-index: 2;
	top: -10px;
	right: -6px;
	font-weight: 400;
}

.mr-au{
	margin: auto;
}

.left-button{
	left: 2%;
}

.right-button{
	right: 2%;
}

.font-40{
	font-size: 40px;
}

.font-30{
	font-size: 30px;
	margin-left: 5px;
}

.font-20{
	font-size: 20px;
	margin-left: 5px;
}

.font-35{
	font-size: 35px;
	margin-left: 5px;
}

.font-50{
	font-size: 50px;
}

.mt-10{
	margin-top: 10px;
}

.mt-20{
	margin-top: 20px;
}

.mt-25{
	margin-top: 25px;
}
	
.fw-700{
